	import common from '../../../common/common.js'
	
	export default {
		data() {
			return {
				sjApiUrl:common.sjApiUrl,
				baseUrl:common.baseUrl,
				catid:0,
				shopInfo:[],		//用户信息
				cat:[],
				cat_img:'',
				catList:[],
				catIndex:'',
				cat_fid:0,
				category:[],
				cat_status:0,			
				
			}
		},
		onLoad(option) {
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			that.shopInfo = shopInfo
			console.log(shopInfo)
			var id = option.id
			if(id > 0){
				that.catid = id
			}
			
			that.getCatInfo();
			//that.getMerchantCatList();

		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');
			
			that.getCatInfo();
			//that.getMerchantCatList();
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		
		methods: {
			
			checkboxChange(e) {
				let that = this
                let values = e.detail.value;
					console.log(values);
                that.goods_tags = values
            },		
			//选择商品状态
			tapStatus(e){
				var that = this
				that.cat_status = e.currentTarget.dataset.status
			},
			//提交信息
			formSubmit: function (e) {
				var that = this
				var data = e.detail.value;
				if(that.catIndex != ''){
					var cat_fid = that.catList[that.catIndex].cat_id;
				}else{
					var cat_fid = 0;
				}
				
				var cat_name = data.cat_name
				var cat_img = that.cat_img
				var cat_status = that.cat_status
				var shopInfo = uni.getStorageSync('shopInfo');
				
				
				if(cat_name == ''){
					uni.showToast({ title:'请输入类别名称',duration:1000,icon:'none' })
					return false;
				}
				
				uni.request({
					url: common.sjApiUrl + 'merchant/editCat',
					method: 'GET',
					data: {
						cat_id:that.catid,
						cat_fid:cat_fid,
						cat_name:cat_name,
						cat_img:cat_img,
						cat_status:cat_status,
						cat_shopid:shopInfo.shop_id
					},
					success: res => {
						var data = res.data
						if(data.code == 0){
							uni.showToast({
								title: '操作成功',
								mask: false,
								duration: 1500
							});
							uni.navigateTo({
								url: '/pages/shop/goods/cat',
							});
						}else{
							uni.showToast({
								title: data.msg,
								mask: false,
								duration: 1500
							});
							return false
						}
					},
					fail: () => {},
					complete: () => {}
				});
				
				
			},
			
			  
			//选择商品类k别
			pickerCat(e){
				var that = this
				var index = e.target.value
				that.catIndex = index
				console.log()
				that.cat_fid = that.catList[index].cat_id
			},
			//获取商户商品类型
			getMerchantFistCatList(){
				var that = this
				var shopInfo = uni.getStorageSync('shopInfo');
				uni.request({
					url: common.sjApiUrl + 'merchant/getMerchantFistCatList',
					method: 'GET',
					data: {
						shop_id:shopInfo.shop_id
					},
					success: res => {
						console.log(res);
						// var goods_cid = that.goods_cid
						var data = res.data
						that.catList = data.cat_list
												
						var category = []
						for(var i=0;i<data.cat_list.length;i++){
							category.push(data.cat_list[i].cat_name)
						}
						that.category = category
						console.log(category,"category")
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//商品信息
			getCatInfo(){
				var that = this
				if(that.catid == 0 ){
					that.getMerchantFistCatList();
					return false
				}
				uni.request({
					url: common.sjApiUrl + 'merchant/getCatInfo',
					method: 'GET',
					data: {
						catid:that.catid
					},
					success: res => {
						var data = res.data
						console.log(res.data)
						that.cat = data.cat
						that.cat_img = data.cat.cat_img
						that.cat_status = data.cat.cat_status
						that.cat_fid = data.cat.cat_fid
						that.catList = data.cat_list
						var category = []
						for(var i=0;i<data.cat_list.length;i++){
							if(data.cat.cat_id == data.cat_list[i].cat_id){
								that.catIndex = i;
								console.log(i);
							}
							category.push(data.cat_list[i].cat_name)
						}
						that.category = category
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//
			jumpUrl(e){
				var that = this
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url: url
				});
			},
			
			//修改图标
			changeImg(){
				var that = this
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						//console.log(JSON.stringify(res.tempFilePaths));
						const tempFilePaths = res.tempFilePaths;
						console.log(tempFilePaths);
						//return false;
						uni.uploadFile({
							url: common.sjApiUrl+'merchant/saveCatImg', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'file',
							formData: {
								
							},
							success: (uploadFileRes) => {
								console.log(uploadFileRes.data);
								var jsonDataStr = JSON.parse(uploadFileRes.data);
								that.cat_img = jsonDataStr.uploadfile								
// 								console.log(that.shopInfo)
							}
						});
					}
				});
			},
			
		}
	}
